import { useState,useEffect,useRef,useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './ReadMore.css'
const ReadMore =({children,caseStudy,title,obj})=>{
  const state = useContext(DemocracyStateContext)
  const [ expanded,setExpanded ] = useState(true)
  const [ originalHeight,setOriginalHeight ] = useState(null)
  const closedheight = 60
  const eltRef = useRef(null)

  const getHeight = ()=> {
    setExpanded(true)
    setTimeout(()=>{
      setOriginalHeight(eltRef.current.clientHeight)
      setExpanded(false)
    },2000)
  }

  useEffect(()=>getHeight(),[state.resizeHappened])
  return (
    <main className={`ReadMore ${expanded?'expanded':''}`}>
      <header>
        <h5>Case study</h5>
        <h6>{caseStudy}</h6>
      </header>
      <h4>{title}</h4>
      {Object.keys(obj).map(key=>(
        <div className="obj" key={key}>
          <strong>{key}</strong>
          <span>{obj[key]}</span>
        </div>
      ))}
       <div
       className="ctn"
       style={{"height":expanded?originalHeight:closedheight}}
       ref={eltRef}>{children}</div>
       <button onClick={()=>setExpanded(!expanded)}>Read {expanded?'less':'more'}</button>
    </main>
  )
}

export default ReadMore
