import { useContext,useEffect,useRef,useState } from 'react'
import ReadMore from '../ReadMore'
import Parallax from '../Parallax'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import './DarkContent.css'

const DarkContent =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  const StaticRef = useRef(null)
  const XXXX = "XXXX"

  useEffect(() => {

    dispatch({type:'SET_CONTENT_ELT',payload:StaticRef})
    const staticRefObserver = new IntersectionObserver( ([{ isIntersecting }]) => {
      dispatch({type:'STATIC_IS_HERE',payload:isIntersecting})
    },{threshold:[0]})
    staticRefObserver.observe(StaticRef.current)
    return ()=> staticRefObserver.disconnect()
  }, [])

  return(
    <main ref={StaticRef} className="DarkContent">
      <main className="burst">
        <Parallax/>
        <section>
          <p>To cybercriminals, usernames and passwords are the
          keys to the castle. Once inside a corporate network they
          can start to move around freely, embedding and hiding
          their malicious software.</p>

          <p>They might steal valuable data or upload ransomware.
          They might shut down vital services. Or they might
          simply be seeking attention by causing trouble. The
          dangers posed by hackers infiltrating a single point on
          the network are myriad.</p>

        </section>
        <article className="keepScrolling">Keep scrolling to understand more about how specific challenges were overcome.</article>
      </main>
      <section>
        <h3>The customer detectives</h3>

        <p>An immediate challenge presented itself to the team trying to identify any elections-related Azure customers. There
        is no requirement for a formal relationship with Microsoft or its account teams. – anyone with a credit card can sign
        up for an Azure subscription.</p>

        <p>This kind of flexibility and immediacy is great for customers, but it raises the possibility of a customer using Azure as
        part of their election infrastructure remaining undetected by Microsoft.</p>

        <p>Keen to ensure customers wouldn’t miss out on vitally important support they might need, the team got to work.
        There was a partial list of known Azure elections-specific customers, from similar work that had been carried out for
        the 2018 midterm elections. That was a solid starting point.</p>

        <p>The next step was to reach out to co-workers across the whole of Microsoft and ask them to share details of
        customers engaged in elections activity.</p>

        <p>Another similar wave of activity across Microsoft sought to identify any technology vendors using Azure. There are
        many specialist elections services providers now running their products on Azure. Some are responsible for voting
        machines, for example.</p>

        <p>The team also sought out political committees, campaign offices, and political parties that might also be using
        Azure. Part of that outreach involved Microsoft leveraging its existing relationships with agencies like CISA, the
        Center for Internet Security, and organizations like Defending Digital Campaigns – a nonprofit organization
        providing access to cybersecurity products and advice to help secure political campaign groups, regardless of party
        affiliation. These agencies were asked to talk to their customers and contacts, find out who was using Microsoft
        technology and encourage those customers to make themselves known.</p>

        <p>Between January and the summer of 2020, it was estimated that more than 90% of the work to identify elections
        customers had been completed.</p>

        <h3>Training for awareness and readiness</h3>

        <p>One important aspect of helping those customers who had been identified was to ensure they were getting access
        to relevant training and skills preparations.</p>

        <p>Since 2018, the Cybersecurity and Infrastructure Security Agency had been organizing training for elections officials
        to supplement their cybersecurity knowledge and skills. Microsoft and the Brennan Center for Justice became
        involved, particularly after these courses were disrupted by the Covid-19 pandemic. Delivering high-value training
        online was a vital part of ensuring elections officials stayed on track with their preparations.</p>

        <p>“Something that’s unique, in terms of American elections, is that they are largely administered at a very local level.
        It’s very decentralized,” says Gowri Ramachandran, counsel with the election security team at the Brennan Center.
        The center is headquartered in New York and has an additional presence in Washington, DC.</p>

        <blockquote>“Over the past decade, and especially since the 2016 election, it’s turned into a job that encompasses cybersecurity, physical security, all of those sorts of additional things,”</blockquote>
        <ReadMore
          caseStudy="Brennan Center"
          title="Helping election officials train, prepare, and grow their confidence"
          obj={{
            "Name":"Brennan Center for Justice",
            "Location":"New York; Washington, DC",
            "Mission":"The Brennan Center for Justice works to build an America that is democratic, just and free – for all"
          }}>
          <p>With the Cybersecurity and Infrastructure Security Agency and Microsoft, the Brennan Center delivered a
          series of training courses to county and municipal elections officials from across the U.S.</p>

          <p>There were six focused training activities that covered crisis planning, scenario training and confidence
          building. These included:</p>
          <p>The importance of backing up systems and having a recovery plan in case of ransomware attack.
          If voting machines stopped working, is there an available supply of paper ballots?</p>

          <p>This training would ordinarily be conducted in face-to-face sessions. But with people working remotely
          because of the pandemic, the training had to be reconfigured to work via Microsoft Teams.</p>

          <p>Liz Howard, Senior Counsel with the Election Security team at the Brennan Center, is a former election
          official who has seen first-hand the way security has become a key issue.</p>

          <p>“This has been a tough year for many people for a variety of reasons. But our election officials have been
          an incredible source of inspiration and hope for so many of us,” Howard says. “When I was an election
          official, security was mostly about knowing who had the keys to the room in the basement where the
          voting machines were kept.”</p>

          <p>“Through grit and amazing determination, they have been able to conduct one of the safest and most
          secure elections in our history, in spite of a pandemic, in spite of being under-resourced, and in spite of
          any number of other challenges at the state and local level.”</p>
        </ReadMore>

        <h3>2020: An election year like no other</h3>

        <p>Nationwide a record number of people voted in the 2020 U.S. election – close to 160 million. Of that number, more
        than 100 million ballots were cast during the early voting period. Many states, already familiar with processing
        mail-in ballots, found themselves facing a huge increase in their number, due to the pandemic. In 2016, Idaho had
        less than 200,000 absentee ballots; in November 2020, that number rose to almost 450,000.</p>

        <p>Not only were social distancing measures going to challenge the usual process of voting, the virus was also taking
        its toll on the state’s workforce, many of whom were unable to work or found themselves working remotely.</p>

        <p>Brett Brandon, Idaho state’s cybersecurity strategist, explains the implications that had for security: “Old practices for
        cybersecurity regarded your firewall as the perimeter of the network.” Remote working led to people outside the
        firewall, outside of that perimeter, needing access to the network</p>.

        <p>“We had to facilitate communications and make sure people and processes still worked, while enabling next-level
        security,” he says.</p>

        <p>Remote working also meant that far-reaching cultural and behavioral changes were needed, according to Foster
        Cronyn, Deputy Secretary of State for Idaho.</p>

        <p>“We had to get people thinking differently about security, and the part they played in it,” he says. “Being more
        careful about their email, for example, or ensuring their computer would automatically go into sleep mode if left
        unattended.”</p>

        <p>Idaho was just one of 50 states grappling with similar issues.</p>

        <ReadMore
          caseStudy="Idaho"
          title="Adapting to change and embracing the future"
          obj={{
            "State":"Idaho",
            "Population":"1.78 million",
            "Counties":"44 in total, some with just a few hundred voters and some with many thousands."
          }}>
          <p>In early 2020, while adjusting to remote working and coping with the impact of the pandemic on the health of some of its staff, Idaho embarked on a major IT upgrade project.
          The state was migrating from its on-premise elections platform to one based in the cloud on Microsoft Azure. With the election coming and the pandemic disrupting workflows, the Idaho team faced some pressing deadlines.</p>
          <p>Brett Brandon, the cybersecurity strategist in the state’s IT department, was part of that initiative and aware of the need to be ready for the November election, which would be the first time the new system would be fully adopted.</p>
          <p>“We had to find a way to go through that full migration, understand the security risks, and make sure they were remediated,” he says. “This is where Microsoft had a huge part to play.”</p>
          <p>The Microsoft elections team examined the infrastructure and network in detail, supplying the Idaho team with details of any necessary improvements.</p>
          <p>“It certainly helped us focus on what we needed to concentrate on,” says Foster Cronyn, the Idaho Deputy Secretary of State. “As smart and as educated as we can possibly be, it is essential to partner with individuals and companies that focus on specific areas of technology and to take their expert advice when it is offered in a critical situation.”</p>
          <p>“We’d certainly participate in that kind of operation again, without even giving it a second thought.”</p>
        </ReadMore>

        <h3>‘It’s about constantly being on guard’</h3>

        <p>Heading east about 2,000 miles bring us to Ohio, where cybersecurity was also high on the agenda of Secretary of
        State Frank LaRose, who served in the 101st Airborne Division and the U.S. Special Forces.</p>

        <p>“We had a saying when I was in the Army that the bad guys only have to be right once, but the good guys and gals
        have to be right every single day. It’s all about vigilance. It’s about constantly being on guard,” he says.</p>

        <p>That outlook underpins LaRose’s approach to cybersecurity: Be prepared and stay prepared. In 2019, he was behind
        a security directive for Ohio’s election officials to keep cybersecurity top-of-mind.</p>

        <p>“We’re always preparing for the next thing as it relates to cybersecurity,” he says. “I’m a big believer in checklists. It’s
        the kind of mentality that pilots have – even if you have thousands of flight-hours, you won’t take off unless you’ve
        completed your pre-flight checklist. Every pilot knows that. Something as important as the cybersecurity of our
        elections should be handled with that same level of care.”</p>

        <p>Just as it had in Idaho, the pandemic caused further complications for election planning in Ohio. “Every aspect of
        running an election was made more difficult this year. In many ways it was kind of the worst-case scenario,” LaRose
        says. “I mean, I don’t think we’ve ever faced a more complex or difficult election.</p>

        <p>“One of my key priorities was to maximize early and absentee voting,” he explains. “The more Ohioans we could
        serve during the month before the election, through early voting and absentee voting, the more we could reduce
        crowding at the polling stations.”</p>

        <p>That meant, LaRose says, that by the morning of November 3, 59% of Ohio’s ballots had already been cast before
        the polls opened. By way of a comparison, in 2016 it was around 34%.</p>

        <ReadMore
          caseStudy="Ohio"
          title="Combining a security mindset with technical capability"
          obj={{
            "State":"Brennan Center for Justice",
            "Population":"11.69 million",
            "Counties":"88, ranging in population size from a few thousand to over one million."
          }}>
          <p>On Jan. 12, 2019, Frank LaRose was sworn in as Secretary of State for Ohio. A few months later, in April, he was an international observer of the Ukrainian presidential election. That, and his distinguished military career, gave him a real insight into just how important it was to maintain public confidence in elections.</p>
          <p>“I served for 10 years in the armed forces and I travelled all over the world,” he says. “In some of the countries where I served, people don’t have a high level of faith in their elections and that’s very troubling. It’s easy to lose that trust and it’s very hard to rebuild it.”</p>
          <p>“Our democracy works better when we’re all involved in it and being a voter is a really powerful way to impact the future of our nation. The good news is that elections routinely run so smoothly that people feel like they can take it for granted,” he said, referring to the U.S. elections.</p>
          <p>But LaRose did not take it for granted and remains committed to safeguarding public trust in the institutions and processes of a flourishing democracy. Because of the security directive initiated in 2019, Ohio now has a network of intrusion detection alarms protecting its County Board of Elections. offers real-time monitoring of the Board’s systems and is supported by an ongoing cycle of training for new staff and refresher courses for existing staff.</p>
          <p>Ohio has further embraced the idea that cybersecurity is everyone’s responsibility with the creation of the Ohio Cyber Reserve, which LaRose likens to the National Guard. It’s a reserve force of civilian cybersecurity experts that volunteer as rapid response team in the event of a hack.</p>
          <p>“We can activate the Ohio Cyber Reserve, load them up in a van and send them up to wherever the problem is to start digging in and working on the problem,” LaRose explains</p>
          <p>“I think that it’s fair to say that cybersecurity is about creating the right culture as much as it is about the right technology,” LaRose says. “I mean, it’s that human firewall, so to speak.</p>
          <p>“You can have the best technology in the world, but if you don’t have people trained to do the right thing, who take it seriously, who place an emphasis on its importance, the technology may not be all that helpful.”</p>
        </ReadMore>

        <h3>From customer care to culture change: All in a day’s work</h3>

        <p>Back inside Microsoft, there was also a clear understanding that cybersecurity skills are only one part of what’s needed.</p>

        <p>Ping Look is a senior director in Microsoft’s Detection and Response Team. She sees her role as bridging the gap
        between people, technology and culture, which she believes is a vital part of getting customers ready to handle
        potential online threats.</p>

        <p>“I feel that right now there needs to be a culture shift,” she says. By that she means everyone within an organization
        needs to be more security-minded and to modify their behavior accordingly. “People keep clicking phishing links,”
        Look says. “You can't just blame them for clicking the link if it looks totally legitimate.”</p>

        <p>Elsewhere, checks were made to ensure customers were thinking about the risk to their IT systems from any sudden
        surges in traffic. Karen Intrachat is a principal program manager within the Microsoft Azure CXP team. She explains
        that looking for the potential challenges a customer might encounter in its IT infrastructure helps them to focus on
        the action they need to take.</p>

        <p>“In our resiliency reviews, we might discover a customer is running their election reporting application on a single
        server”,says Intrachat. But what if it goes down? Can anything be done to spread the traffic, and do they have a
        back-up procedure in place?</p>

        <p>“This is an example of a critical situation where not only do you want your application to be highly secure; you also
        want it to withstand any spikes or changes within your infrastructure.”</p>

        <h3>Threat hunters</h3>
        <p>External threats tend to occupy cybersecurity professionals’ minds most. That’s certainly true for Sean Ensz, a Crisis
        Response Manager in the Microsoft Security Response Center (MSRC).</p>

        <p>He’s a threat hunter and as such is at the forefront of helping spot problems before they affect customers. He
        believes cloud technology has made security efforts a shared responsibility between customers and their technology
        providers.</p>

        <p>For example, in August 2020, Microsoft released a software patch for something known in security circles as the
        ZeroLogon vulnerability, or CVE-2020-1472. But the time it can take to install such patches meant some
        organizations remained potentially vulnerable for weeks, maybe months.</p>

        <p>“We issued warnings about this to state and local governments,” Ensz says. “To ensure they understood it was
        important to install the patch.”</p>

        <p>The MSRC was also involved with spotting potential problems in the run-up to the election. “A few weeks before the
        election, MSRC was engaged to help run a daily effort around threat hunting,” Ensz says.</p>

        <blockquote>“We got threat hunting groups from around the company engaged on daily calls, set up a
        team site for them to use, and then we sent out summaries on a daily basis, as well as a
        weekly roll-up.”</blockquote>

        <p>Drawing people and resources together from across Microsoft was an important aspect of the work, as it means
        individuals get to learn from one another’s expertise. “The analogy we use is not being able to see the forest for the
        trees. Individual groups are heads-down in their own areas of expertise and they do a really good job in those areas.
        But they can have a harder time seeing the bigger picture,” he continues.</p>


        <blockquote>{`“One thing that I always find encouraging is, regardless of how difficult the incidents, that
        really people come together. They're willing to collaborate and share what they
        know and learn.”`}</blockquote>

        <h3>Strength in numbers</h3>
        <p>This culture of collaboration extended beyond Microsoft’s internal teams so that software vendor partners like BPro
        were integrated into its work. BPro is a software and services business based in South Dakota that offers voter
        registration and election management that run on Microsoft Azure.</p>

        <ReadMore
          caseStudy="BPro Inc"
          title="Connected to peace of mind"
          obj={{
            "Name":"BPro Inc",
            "Population":"Pierre, South Dakota",
            "Specialization":"A software services company providing on-premise and cloud-based election solutions to states and localities across the U.S."
          }}>
          <p>BPro’s 25 team members handle voter registration, election management, information portals, campaign finance solutions and election reporting systems in 15 states and around 100 different counties.</p>
          <p>This company creates systems that pull data from sources like death registrations and help streamline the process of updating voter lists. The systems run on Microsoft Azure to offer scalability to its clients.</p>
          <p>Many aspects of everyday life require individuals to verify their identity with a signature. Over their lifetime, a typical U.S. citizen may sign dozens of official documents. Those signatures are an important part of the identity validation process and the more examples of an individual’s signature there are, the better.</p>
          <p>Having a lifetime’s worth of signatures available can be a logistical challenge. It would effectively mean keeping scanned copies of potentially hundreds of official documents per citizen. The storage requirements for such an undertaking are significant.</p>
          <p>“The systems we host in Azure have virtually unlimited storage capacity,” says George Munro, Government Outreach Director at BPro. “That means any official records with a signature can be captured by our systems and all those signatures are automatically stored.”</p>
          <p>Munro explains that the ability to offer enhanced data and document storage is only one of the benefits BPro and its clients get from the relationship with Microsoft. “At a moment’s notice, during the election, just having Microsoft on the phone, helping us address any issues that comes up is something that gives our customers peace of mind. They know it’s not just these folks from South Dakota who are working on the issue, it’s those folks in Redmond, as well,” referring to the location of Microsoft’s corporate headquarters.</p>
        </ReadMore>

        <p>“The systems we host in Azure have virtually unlimited storage capacity,” says George Munro, government outreach
        director at BPro. “That means any official records with a signature can be captured by our systems and all those
        signatures are automatically stored.” That kind of scalability is something BPro and its customers rely on.</p>

        <p>By ensuring BPro, and other key vendor partners, were included in the work of the elections team, Microsoft could
        provide access to greater support and faster turnaround times.</p>

        <p>And the lessons learnt by the Defending Democracy team through all this varied work have been gathered into a
        unified set of features, which is now being formally offered through Azure for Elections, a product which {XXXX}.</p>


        <h3>In a year like no other, a day like any other day</h3>

        <p>The careful planning and proactive action taken by the Microsoft elections team and its partners meant many
        potential threats were avoided.</p>

        <p>But the work of defending democracy didn’t end on a certain Tuesday in November. As the period between Nov. 3
        and Jan. 20 has shown, elections – even the most secure – are only one part of a much larger picture. A healthy,
        open democracy requires so much more than its citizens to turn out and vote.</p>

        <p>News emerged in the earliest days of 2021 detailing how state-sponsored hacking groups were quietly undertaking
        what the New York Times described as “the widespread intrusion into government and corporate networks”
        throughout much of 2020.</p>

        <p>These attacks and incidents, including the attack on the Capitol, further demonstrate the need for a broader
        understanding of risks across the board – along with an acceptance that combating them is everyone’s joint
        responsibility.</p>

        <p>Naturally, cybersecurity is just one element – but it remains a crucial one. By the time campaigning for 2024 is
        underway, the technology hackers rely on will have grown more powerful and the hackers themselves will have
        become more sophisticated. The wider societal and geopolitical landscape will have evolved too.</p>

        <p>The need to defend democracy will be as real as ever.</p>
      </section>
    </main>
  )
}

export default DarkContent
