import { useEffect,useState,useCallback,useContext } from 'react'
import debounce from 'lodash/debounce'
import Story from '../Story/'
import Nav from '../Nav/'
import Anims from '../Anims/'
import Content from '../Content/'
import Credit from '../Credit/'
import DarkContent from '../DarkContent/'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import './NormalContent.css'

const NormalContent =()=>{
  const state = useContext(DemocracyStateContext)
  const dispatch = useContext(DemocracyDispatchContext)
  const { direction,currentStory,mobile,accessible,isScrollable } = state
  const [y, setY] = useState(window.scrollY)


  const handleScroll = e=>{
    sendScrollPercent()
    handleNavigation(e)
    debouncedScene()
  }

const debouncedScene = useCallback(debounce(()=>dispatch({type:'SET_SCROLLABLE'}),50),[])

const handleNavigation = useCallback(
  e => {
    const window = e.currentTarget
    const up = y > window.scrollY
    if (up) {
      if(up!==direction)dispatch({type:'SET_DIRECTION',payload:true})
    } else {
      if(up!==direction)dispatch({type:'SET_DIRECTION',payload:false})
    }
    setY(window.scrollY)
  }, [y]
)


  const sendScrollPercent = ()=>{
    const doc = document.documentElement
    const winScroll = doc.scrollTop
    const winHeight = doc.scrollHeight - doc.clientHeight
    const scrolled = Math.round(winScroll / winHeight * 100)
    dispatch({type:'SET_SCROLL',payload:doc.scrollTop})
    dispatch({type:'SET_PERCENT',payload:scrolled})
  }


  useEffect(()=>{
    setY(window.scrollY)
    window.addEventListener('scroll',handleScroll)
    if(mobile!==window.innerWidth<=1024)dispatch({type:'SET_MOBILE',payload:window.innerWidth<=1024})
    return ()=> window.removeEventListener('scroll',handleScroll)
  },[handleNavigation])


  return (
    <div className={`NormalContent wrapper ${accessible?'hide':''}`}>
      <main className="Democracy">
        <div id="Intro"/>
        <Anims/>
        <Story/>
        <div id="Static"><DarkContent/></div>
        <Credit/>
      </main>
      <Nav/>
      <Content/>
    </div>
  )
}

export default NormalContent
