import { useContext,useEffect,useState,useRef } from 'react'
import lyr1 from '../../assets/images/parallax_static_lyr_1.svg'
import lyr2 from '../../assets/images/parallax_static_lyr_2.svg'
import lyr3 from '../../assets/images/parallax_static_lyr_3.svg'
import lyr4 from '../../assets/images/parallax_static_lyr_4.svg'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Parallax.css'

const Parallax =()=>{
  const state = useContext(DemocracyStateContext)
  const { scroll,direction } = state
  const [ showParallax,setShowParallax ] = useState(false)
  const [ mouseX,setMouseX ] = useState(0)
  const [ mouseY,setMouseY ] = useState(0)
  const [ scrollY,setScrollY ] = useState(0)
  const ParallaxRef = useRef(null)

  const handleMouseMove = e => {
    const { clientX,clientY } = e
    setMouseX(-clientX + (window.innerWidth/2))
    setMouseY(-clientY + (window.innerHeight/2))
  }

  useEffect(() => {
    if(showParallax)window.addEventListener('mousemove',handleMouseMove)
    else window.removeEventListener('mousemove',handleMouseMove)
    return ()=> window.removeEventListener('mousemove',handleMouseMove)
  },[showParallax])

  useEffect(() => {
    const parallaxRefObserver = new IntersectionObserver( ([{ isIntersecting }]) => {
      setShowParallax(isIntersecting)
    },{threshold:[0]})
    parallaxRefObserver.observe(ParallaxRef.current)
    return ()=> parallaxRefObserver.disconnect()
  }, [])

  const handleScroll = ()=>{
    const y = direction ? scrollY + 1 : scrollY - 1
    setScrollY(y)
  }

  useEffect(()=>{
    if(showParallax)handleScroll()
  },[scroll])


  return(
    <div ref={ParallaxRef} className="Parallax">
      <div style={{"transform":"translate3d("+ -mouseX/80 +"px,"+ -mouseY/80 +"px,0)"}}><img style={{"transform":"translateY("+ (scrollY - (scrollY /2))/200 +"%)"}} src={lyr1} alt="paralax1"/></div>
      <div style={{"transform":"translate3d("+ -mouseX/60 +"px,"+ -mouseY/60 +"px,0)"}}><img style={{"transform":"translateY("+ (scrollY - (scrollY /2))/5 +"%)"}} src={lyr2} alt="paralax2"/></div>
      <div style={{"transform":"translate3d("+ -mouseX/40 +"px,"+ -mouseY/40+"px,0)"}}><img style={{"transform":"translateY("+ (scrollY - (scrollY /2))/1.5 +"%)"}} src={lyr3} alt="paralax3"/></div>
      <div style={{"transform":"translate3d("+ -mouseX/20+"px,"+ -mouseY/20+"px,0)"}}><img style={{"transform":"translateY("+ (scrollY - (scrollY /2))/1.1 +"%)"}} src={lyr4} alt="paralax4"/></div>
    </div>
  )
}

export default Parallax
