import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './IntroText.css'
const IntroText =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="IntroText">
       <article>
        <p>Do you remember where you were when the outcome of the 2020 U.S. presidential election was announced? How
        about when President Biden’s victory was formally confirmed? Or on the day a mob attacked the Capitol?</p>

        <p>These are some of the most pivotal, most well-documented moments in the election. But there is another,
        lesser-known story. It tells of the many months of preparation undertaken by hundreds of people behind the
        scenes. Of how, in the middle of a pandemic, teams of highly experienced people came together to protect the
        integrity of each vote.</p>

        <p>This is a behind-the scenes look at that team, who, away from the media spotlight and campaign trail, worked
        tirelessly to protect each vote in the run-up to the 2020 presidential election. It included state officials, providers of
        election technology and Microsoft’s own team of experts.</p>

        <p>This network of people fended off cyberattacks and ensured that voting mechanisms were functioning correctly.
        They kept voter registration websites safe and ensured a reliable exchange of important information – including the
        final results.</p>

        <p>No matter how you look at it, this team had to get it right – the accuracy of the results and the integrity of the
        election were depending on them.</p>
       </article>
    </section>
  )
}

export default IntroText
