import { useContext,useEffect,useState } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import prev from '../../assets/icons/prev.svg'
import next from '../../assets/icons/next.svg'
import './Nav.css'
const Nav =()=>{
  const state = useContext(DemocracyStateContext)
  const dispatch = useContext(DemocracyDispatchContext)
  const { scenes,currentStory,direction,accessible } = state
  const [ menuOpen,setMenuOpen ] = useState(false)
  const handleNavigate = e => {
    e.preventDefault()
    dispatch({type:'NAVIGATE',payload:e.target.getAttribute('scene')})
    if(!e.target.classList.contains('dontClose')) setMenuOpen(!menuOpen)
    setTimeout(()=>dispatch({type:'ENABLE_NAVIGATE'}),3000)
  }

  let href = '#Shadow'
  const manageScroll = ()=>{
    if(!accessible){
      switch (currentStory) {
        case 0:window.scrollTo({top: 0});break;
        //case 14: if(direction)window.scrollTo({top:window.innerHeight*15});break;
        case 15:window.scrollTo({top: window.innerHeight*(currentStory+1.4)});break;
        default:
      }
    }
  }

  useEffect(()=>{
    manageScroll()
  },[currentStory])

  return(
    <main className={`Nav ${menuOpen?'open':''}`}>
      <button
        onClick={()=>setMenuOpen(!menuOpen)}
        type="button"
        aria-label="Expand to see navigation elements"
        initialstate-label="Expand to see navigation elements"
        togglestate-label="Close navigation"
        aria-expanded={menuOpen}>
        <span>Menu</span>
      </button>
     <nav>
        <ul
          onMouseLeave={()=>setMenuOpen(false)}
          onClick={handleNavigate}>
          {false&&<li><a href="#ws">{currentStory}</a></li>}
          <li className={currentStory===0?'selected':null}><a scene={0} href="#Intro">Back to Top</a></li>
          <li className={currentStory>=1 && currentStory<=5?'selected':null}><a scene={1} href="#NetworkTitle">Network of collaborators</a></li>
          <li className={currentStory>=6 && currentStory<=9?'selected':null}><a scene={6} href="#History">History lesson from 2016</a></li>
          <li className={currentStory>=10 && currentStory<=15?'selected':null}><a scene={10} href="#History">Responding to the threat</a></li>
          <li className={currentStory>15?'selected':null}><a scene={15} href="#Static">An election like no other</a></li>
          <li className="btn dontClose">
            <a
              className={`dontClose ${currentStory===0?'disabled':null}`}
              scene={currentStory - 1}
              href={currentStory === 0 ? '#Intro' : href}>
              <img
                className="dontClose"
                scene={currentStory - 1}
                width="18"
                src={prev}
                alt="prev"/>
            </a>
            <a
              className={`dontClose ${currentStory===scenes.length-1?'disabled':null}`}
              scene={currentStory + 1}
              href={currentStory === 14 ? '#Static' : href}>
              <img
                className="dontClose"
                scene={currentStory + 1}
                width="18"
                src={next}
                alt="next"/>
            </a>
          </li>
        </ul>
     </nav>
   </main>
  )
}

export default Nav
