import { useContext,useEffect,useState } from 'react'
import LottieAnim from '../LottieAnim/'
import avatars from '../../assets/lottie/avatars.json'
import maptriangle from '../../assets/lottie/triangle.json'
import to_icons from '../../assets/lottie/to_icons.json'
import radar from '../../assets/lottie/radar.json'
//import speech from '../../assets/lottie/speech.json'
import shadow from '../../assets/lottie/shadow.json'
import padlock from '../../assets/lottie/padlock_bigger.json'
import matrix from '../../assets/lottie/matrix_2.json'
import darkweb from '../../assets/lottie/darkweb.json'
import HeadIntro from '../HeadIntro'
import Ethan1 from '../Ethan1'
import Ethan2 from '../Ethan2'
import Author from '../Author'
import Conclusion from '../Conclusion'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Anims.css'
const Anims =()=>{
    const state = useContext(DemocracyStateContext)
    const [ intro,setIntro ] = useState(false)
    const { currentStory,scroll,direction } = state
    const triangle = currentStory===0 ||
                     currentStory===1 ||
                     currentStory===2 ||
                     currentStory===3 ||
                     currentStory===6
    const isShadow = currentStory>=7 && currentStory<=9
    useEffect(()=>setTimeout(()=>setIntro(true),200),[])

  return(
    <main className={`Anims ${direction?'forward':'backwards'} ${currentStory>= 15? 'noShow':''}`}>

      <div className={`triangles intro ${currentStory>= 2?'big':''}`}>
        {intro&&<LottieAnim looped={false} playAnim={triangle} data={maptriangle}/>}
      </div>

      <LottieAnim looped={false} data={avatars} playAnim={currentStory === 4}/>
      {/*<LottieAnim looped={false} data={speech} playAnim={currentStory === 6}/>*/}
      <div className={`shadow ${currentStory>=7?'show':''}`}>
        <LottieAnim looped={true} data={shadow} playAnim={isShadow}/>
      </div>
      <LottieAnim playAnim={currentStory===5} looped={false} data={to_icons}/>
      <div className={`triangles radar ${currentStory>=6?'bigsRadar':''}`}>
        <LottieAnim
          looped={true}
          playAnim={(currentStory>=4)&&(currentStory<=5)}
          data={radar}/>
      </div>
      <div className="padlock"><LottieAnim playAnim={currentStory>=10&&currentStory<=11} looped={false} data={padlock}/></div>
      <LottieAnim playAnim={currentStory===12||currentStory===13} looped={false} data={matrix}/>
      <div className={`darkweb ${currentStory===15?'out':''}`}>
        <LottieAnim playAnim={currentStory>=13&&currentStory<=15} looped={false} data={darkweb}/>
      </div>

      <div className="OutContent">
        {(currentStory===0 || currentStory === 1)&&<HeadIntro/>}
        {currentStory===12&&<Ethan1/>}
        {currentStory===13&&<Ethan2/>}
        {currentStory===14&&<Conclusion/>}
      </div>
      {currentStory>=11&&<Author/>}




    </main>
  )
}

export default Anims
