import { useContext } from 'react'
import './Password.css'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import DemocracyStateContext from '../../context/DemocracyStateContext'
const Password =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  const state = useContext(DemocracyStateContext)
  return (
    <main className="Password">
      <h1>Please enter the password</h1>
      <div>
        <input
          type="password"
          onChange={e=>dispatch({type:'SET_PASSWORD',payload:e.target.value})}
          value={state.passValue}/>
      </div>
      <section>
        <h2>V1</h2>
        <article>
          <div>
            <h3>Original brief</h3>
            <h4>08/01/2021</h4>
            <ol>
              <li>Header with vote video</li>
              <li>Animated experience</li>
              <li>Custom mobile experience</li>
              <li>Accessible content</li>
              <li>Static content</li>
              <li>Mobile design</li>
              <li>Mobile experience</li>
            </ol>
          </div>
          <div>
            <h3>Bug fixes</h3>
            <h4>12/01/2021</h4>
            <ol>
              <li>Only one scrolling action required to navigate between scenes</li>
              <li>Fixed inadvertent scene skipping while navigating backwards</li>
              <li>Text is no longer cut off by graphics in circuit board scenes</li>
            </ol>
          </div>
        </article>
        <h2>V2</h2>
        <article>
          <div>
            <h3>New header</h3>
            <h4>21/01/2010</h4>
            <ol>
              <li>New hero header design intergrated with the animated content</li>
              <li>Title text animation</li>
            </ol>
          </div>
          <div>
            <h3>Animated and static Content</h3>
            <h4>21/01/2010</h4>
            <ol>
              <li>Design updates on animated content</li>
              <li>New design on "static" content part</li>
              <li>Text boxes</li>
            </ol>
          </div>
        </article>
      </section>
    </main>
  )
}

export default Password
