import { useContext,useState,useEffect,useRef } from 'react'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import './Story.css'

const Story =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  const sceneOne = useRef(null)
  useEffect(()=>dispatch({type:'SET_SCENE_REF',payload:sceneOne.current.getBoundingClientRect().y}),[])
  return(
    <main
      ref={sceneOne}
      id="Story"
      className="Story">
      <div style={{"height":"200vh"}}/>
      <section>
        <p id="NetworkTitle">NetworkTitle</p>
        <p id="NetworkContent">NetworkContent</p>
        <p id="Scanner_one">Scanner_one</p>
        <p id="Connections">Connections</p>
        <p id="Preperations">Preperations</p>
        <p id="History">History</p>
        <p id="HistoryContent">HistoryContent</p>
        <p id="Politics">Politics</p>
        <p id="Shadow">Shadow</p>
        <p id="Persistent">Persistent</p>
        <p id="PersistentContent">PersistentContent</p>
        <p id="Ethan_one">Ethan_one</p>
        <p id="Ethan_two">Ethan_two</p>
        <p id="Conclusion">Conclusion</p>
      </section>
    </main>
  )
}

export default Story
