import { useContext,useState,useEffect } from 'react'
import scroll from '../../assets/lottie/scroll_white.json'
import LottieAnim from '../LottieAnim/'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './HeadIntro.css'
const HeadIntro =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
    const [ intro,setIntro ] = useState(false)
    useEffect(()=>setIntro(true),[])
  return(
    <section className={`HeadIntro center ${(currentStory === 0)&&intro ?'show':''}`}>
      <h1>
        <span><em>Keeping your vote</em></span>
        <span><em>safe and secure:</em></span>
        <span><em>An inside story from</em></span>
        <span><em>the 2020 election</em></span>
      </h1>
      <a href="#NetworkTitle"><LottieAnim playAnim={true} looped={true} data={scroll}/></a>
    </section>
  )
}

export default HeadIntro
