import { useContext,useEffect,useCallback } from 'react'
import debounce from 'lodash/debounce'
import microsoft from '../../assets/images/microsoft.webp'
import mslogo from '../../assets/images/mslogo.webp'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Topbar.css'
const Topbar =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  const state = useContext(DemocracyStateContext)
  const { currentStory,scenes,resizeHappened,direction,goDark } = state
  const debouncedResize = useCallback(debounce(()=>dispatch({type:'RESIZE_HAPPENED'}),500),[])
  const handleResize = ()=>debouncedResize()

  useEffect(()=>{
    window.addEventListener("resize",handleResize)
    return ()=> window.removeEventListener("resize",handleResize)
  },[])

  useEffect(()=>{
    setTimeout(()=>{
      if (direction && goDark || currentStory === 0) dispatch({type:'SET_DARK_TOP',payload:false})
      else if(!goDark) dispatch({type:'SET_DARK_TOP',payload:true})
    },2000)
  },[currentStory > 0])

  return (
    <main className={`Topbar ${goDark ? 'bg':''}`}>
      <span>
        <span><img src={mslogo} alt="microsoft"/></span>
        <span><img src={microsoft} alt="microsoft"/></span>
      </span>
      <a
        href="https://news.microsoft.com/on-the-issues"
        target="_blank"
        rel="noreferrer">On the Issues</a>
    </main>
  )
}
export default Topbar
